.btn{
    background-color: black;
    color: blanchedalmond;
    padding: 0.7em 1.2em;
    text-decoration: none;
    border: none;
    border-radius: 5%;
    cursor: pointer;
    transition: 0.5s;
}

.btn:hover{
    box-shadow: 0px 0px 5px yellow inset;
    text-shadow: 0px 0px 5px yellow;
}